import React, { Component } from 'react';
import { Bar, Line } from 'react-chartjs-2';
import {
    Badge,
    Button,
    ButtonDropdown,
    ButtonGroup,
    ButtonToolbar,
    Card,
    CardBody,
    CardFooter,
    CardHeader,
    CardTitle,
    Col,
    Dropdown,
    DropdownItem,
    DropdownMenu,
    DropdownToggle,
    Progress,
    Row,
    Table,
} from 'reactstrap';
import Widget03 from '../views/Widgets/Widget03';
import { CustomTooltips } from '@coreui/coreui-plugin-chartjs-custom-tooltips';
import { getStyle, hexToRgba } from '@coreui/coreui/dist/js/coreui-utilities';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import AccountStatsRedux from '../Redux/Report/AccountStatsRedux';
import UtilChart from '../Util/Chart';
import util from '../util';

const brandPrimary = getStyle('--primary')
const brandSuccess = getStyle('--success')
const brandInfo = getStyle('--info')
const brandWarning = getStyle('--warning')
const brandDanger = getStyle('--danger')

// Card Chart 1
const cardChartData1 = {
    labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
    datasets: [
        {
        label: 'My First dataset',
        backgroundColor: brandPrimary,
        borderColor: 'rgba(255,255,255,.55)',
        data: [65, 59, 84, 84, 51, 55, 40],
        },
    ],
};

const cardChartOpts1 = {
    tooltips: {
        enabled: false,
        custom: CustomTooltips
    },
    maintainAspectRatio: false,
    legend: {
        display: false,
    },
    scales: {
        xAxes: [
        {
            gridLines: {
            color: 'transparent',
            zeroLineColor: 'transparent',
            },
            ticks: {
            fontSize: 2,
            fontColor: 'transparent',
            },

        }],
        yAxes: [
        {
            display: false,
            ticks: {
            display: false,
            min: Math.min.apply(Math, cardChartData1.datasets[0].data) - 5,
            max: Math.max.apply(Math, cardChartData1.datasets[0].data) + 5,
            },
        }],
    },
    elements: {
        line: {
        borderWidth: 1,
        },
        point: {
        radius: 4,
        hitRadius: 10,
        hoverRadius: 4,
        },
    }
}

//Random Numbers
function random(min, max) {
    return Math.floor(Math.random() * (max - min + 1) + min);
}

var elements = 27;
var data1 = [];
var data2 = [];
var data3 = [];

for (var i = 0; i <= elements; i++) {
    data1.push(random(50, 200));
    data2.push(random(80, 100));
    data3.push(65);
}

const mainChart = {
    labels: ['Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa', 'Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa', 'Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa', 'Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa', 'Su'],
    datasets: [
        {
        label: 'My First dataset',
        backgroundColor: hexToRgba(brandInfo, 10),
        borderColor: brandInfo,
        pointHoverBackgroundColor: '#fff',
        borderWidth: 2,
        data: data1,
        },
        {
        label: 'My Second dataset',
        backgroundColor: 'transparent',
        borderColor: brandSuccess,
        pointHoverBackgroundColor: '#fff',
        borderWidth: 2,
        data: data2,
        },
        {
        label: 'My Third dataset',
        backgroundColor: 'transparent',
        borderColor: brandDanger,
        pointHoverBackgroundColor: '#fff',
        borderWidth: 1,
        borderDash: [8, 5],
        data: data3,
        },
    ],
};

const mainChartOpts = {
    tooltips: {
        enabled: false,
        custom: CustomTooltips,
        intersect: true,
        mode: 'index',
        position: 'nearest',
        callbacks: {
        labelColor: function(tooltipItem, chart) {
            return { backgroundColor: chart.data.datasets[tooltipItem.datasetIndex].borderColor }
        }
        }
    },
    maintainAspectRatio: false,
    legend: {
        display: false,
    },
    scales: {
        xAxes: [
        {
            gridLines: {
            drawOnChartArea: false,
            },
        }],
        yAxes: [
        {
            ticks: {
            beginAtZero: true,
            maxTicksLimit: 5,
            stepSize: Math.ceil(250 / 5),
            max: 250,
            },
        }],
    },
    elements: {
        point: {
        radius: 0,
        hitRadius: 10,
        hoverRadius: 4,
        hoverBorderWidth: 3,
        },
    },
};

class Dashboard extends Component {
    constructor(props) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.onRadioBtnClick = this.onRadioBtnClick.bind(this);

        this.state = {
        dropdownOpen: false,
        radioSelected: 2,
        };
    }
    componentDidMount = () => {
        this.props.actions.accountStats.stats();
    }
    toggle() {
        this.setState({
        dropdownOpen: !this.state.dropdownOpen,
        });
    }
    onRadioBtnClick(radioSelected) {
        this.setState({
        radioSelected: radioSelected,
        });
    }
    renderStatBox = (title, key, style = brandPrimary, bgClass, type = 'total', height = 70) => {
        if (this.props.accountStats.loading === false){
            const stats = this.props.accountStats.data[key];
            const chartDataset = UtilChart.getChartLine(title, stats, style);
            let value = null;

            if (type === 'last'){
                value = Object.values(stats).filter(value => {return value > 0;}).pop();
            }
            else if (type === 'total'){
                value = Object.values(stats).reduce((carry, value) => {return carry + value;})
            }

            const classes = 'text-white ' + bgClass;

            return <Card className={classes}>
                <CardBody className="pb-0">
                    <div className="text-value">{util.formatMoney(value)}</div>
                    <div>{title}</div>
                </CardBody>
                <div className="chart-wrapper mx-3" style={{ height: '70px' }}>
                    <Line data={chartDataset} options={UtilChart.getChartLineOptions(chartDataset)} height={height} />
                </div>
            </Card>
        }
    }
    render() {

        return (
        <div className="animated fadeIn">
            <Row>
                <Col xs="12" sm="6" lg="3">
                    {this.renderStatBox('Saldo Cuenta', 'amounts', brandInfo, 'bg-primary', 'last')}
                </Col>
                <Col xs="12" sm="6" lg="3">
                    {this.renderStatBox('Ingresos', 'incomes', brandWarning, 'bg-info', 'total')}
                </Col>
                <Col xs="12" sm="6" lg="3">
                    {this.renderStatBox('Egresos', 'outcomes', brandInfo, 'bg-warning', 'total')}
                </Col>
                <Col xs="12" sm="6" lg="3">
                    {this.renderStatBox('Balance', 'balances', brandInfo, 'bg-success', 'total')}
                </Col>
            </Row>
        </div>
        );
    }
}

const mapStateToProps = ({ accountStats }) => ({
    accountStats,
});

const mapDispatchToProps = (dispatch) => ({
	actions: {
        accountStats: bindActionCreators(AccountStatsRedux, dispatch),
    }
});

export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);



